.friend {
  display: flex;
  align-items: center;
  padding: 10px 0 10px 15px;
  box-shadow: 0px 1px 7px 1px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 0px 1px 7px 1px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 1px 7px 1px rgba(0, 0, 0, 0.75);
  background-color: #f7f7f7;

  &__avatar {
    display: block;
    width: 50px;
    height: 50px;
    margin-right: 15px;
    border-radius: 10px;
  }

  &__name {
    font-size: 20px;
  }
}

.status {
  display: block;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin-right: 15px;
}

.online {
  background-color: rgb(101, 173, 87);
}
.offline {
  background-color: rgb(231, 97, 85);
}
